<template>
  <div class="char_container">
    <div class="char" v-for="(character, key) in roster" :key="key">
      <img @click="getchar(key)" :src="'/character/profile/'+character.ID+'.jpg'" :alt="character.Name" />
    </div>
  </div>
</template>

<script>
export default {
  mounted() {
    this.$store.dispatch("character/fetch_all");
  },
  computed: {
    roster() {
      return this.$store.getters["character/roster"];
    }
  },
  methods: {
    getchar(key){
      this.$store.dispatch("character/select_char", key)
    }
  }
};
</script>

<style lang="scss" scoped>
.char_container {
  width: 95%;
  height: 70%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
}

.char {
  margin: 0.3rem;
  padding: 0;
  height: 75px;
  border: 2px solid white;
  border-radius: 5px;
  box-shadow: 0px 3px 3px rgba(0,0,0,0.2);
  cursor: pointer;
}
</style>